.game{
    display: flex;
    margin: 10px auto;
    align-items: center;
    justify-content: center;
    text-align: center;
  }

.grid{
    margin: auto;
    border: 4px solid black;
    background-color: #f8f8f8e1;
}

.cell{
    margin: 0;
    padding: 0;
    width: 3.2rem;
    height: 3.2rem;
    border: none;
}

.cell-current{
  background-color: lightgreen;
}

.cell-empty {

}

.cell-wall{
  background-color: rgb(90, 90, 90);
}

.controls{
  margin-top: 5px;
}

.button{
  width: 3.8rem;
  height: 3.8rem;
  border-radius: 10px;
  cursor: pointer;
  border: 1px solid lightgrey;
  background-color: black;
  color: gold;
}

.button:hover{
  background-color: lightgrey;
}

.row-grid{
  height: 3.2em;
}

img {
  max-width: 100%;
  max-height: 100%;
}